.ParticipantRow-container {
  display: grid;
  grid-template-columns: 1fr 2fr 1fr 1fr;
  background: white;
}

.ParticipantRow-container > div {
  border-top: 1px solid #dddddd;
  color: #505050;
  font-size: 16px;
  line-height: 24px;
  font-weight: 400;
  padding: 24px;

  overflow: hidden;
  white-space: nowrap;
  -o-text-overflow: ellipsis;
     text-overflow: ellipsis;
}

.ParticipantRow-container > div.ParticipantRow-actions {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: distribute;
      justify-content: space-around;
}



